/* src/SenseBox.css */
.sense-box {
  border: 2px solid #555;
  border-radius: 8px;
  background-color: #282c34;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-width: 0;
  min-height: 0;
}

.sense-box .sense-name {
  text-align: center;
  margin-top: 0;
  color: #61dafb;
  font-size: 1.2rem;
}

.events-container {
  flex-grow: 1; 
  display: flex;
  flex-direction: column-reverse; 
  justify-content: flex-start; 
  align-items: center;

  overflow: hidden;
  min-height: 0;
}

.event-text {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 0.20rem;
  border-radius: 4px;
  margin: 0.1rem 0;
  width: 90%;
  text-align: center;
  font-size: 0.8rem;
}
